import React from 'react';
import { TableLogItem } from '/shared/types';
import SymfonyData from '@/pages/main/components/SymfonyData';

export default React.memo(function Table({ item }: { item: TableLogItem }) {
    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            {Object.keys(item.values).length ? (
                <dl className="markup-dl table">
                    {Object.keys(item.values).map((key) => {
                        return (
                            <React.Fragment key={key}>
                                <dt>{key}</dt>
                                <SymfonyData id={item.id} content={item.values[key]} as="dd" expand={item.expand} />
                            </React.Fragment>
                        );
                    })}
                </dl>
            ) : (
                <code>Empty</code>
            )}
        </div>
    );
});
