import { Group } from '/shared/types';
import { useEffect, useMemo, useRef, useState } from 'react';
import { Virtuoso, VirtuosoHandle } from 'react-virtuoso';
import styles from './Logs.module.scss';
import Placeholder from './Placeholder';
import LogItemGroup from './Group';
import usePreferences from '@/stores/preferences';
import { DownArrowIcon } from '@/components/Icons';
import { COLORS, useFilters } from '@/stores/filter';

export default function LogTable({ groups }: { groups: Map<string, Group> }) {
	const virtuosoRef = useRef<VirtuosoHandle>(null);
	const isAtBottom = useRef(true);
	const [newItems, setNewItems] = useState(false);

	const lastAmountOfItems = useRef(0);
	const timeoutRef = useRef<any>(null);

	const { scrollOnLog } = usePreferences()
	const { query, colorFilters, reset } = useFilters();

	const filteredGroups = useMemo(() => {
		const groupsArray = Array.from(groups.values());
		if (!colorFilters && !query) {
			return groupsArray
		}
		return groupsArray.filter(group => {
			if (colorFilters && !(colorFilters & COLORS[group.color])) return false;
			if (query && !JSON.stringify(group).toLowerCase().includes(query.toLowerCase())) return false;

			return true;
		});
	}, [groups, colorFilters, query]);

	const showResetFilters = groups.size > 0 && filteredGroups.length === 0 && (colorFilters || query);


	function scrollToBottom() {
		virtuosoRef.current?.scrollToIndex({
			index: 'LAST',
			behavior: 'smooth',
			align: 'end',
		});
	}

	useEffect(() => {
		if (lastAmountOfItems.current !== filteredGroups.length) {
			if (scrollOnLog) {
				clearTimeout(timeoutRef.current);
				timeoutRef.current = setTimeout(scrollToBottom, 100);
			} else {
				clearTimeout(timeoutRef.current);
				timeoutRef.current = setTimeout(() => {
					if (!isAtBottom.current) {
						setNewItems(true);
					}
				}, 100);
			}
		}

		lastAmountOfItems.current = filteredGroups.length;
	}, [filteredGroups, scrollOnLog]);

	function onClickScrollButton() {
		setNewItems(false);
		scrollToBottom();
	}

	return (
		<div className={styles.LogTable}>
			<Virtuoso
				ref={virtuosoRef}
				atBottomStateChange={(atBottom) => {
					isAtBottom.current = atBottom;
					if (atBottom) setNewItems(false);
				}}
				totalCount={filteredGroups.length}
				overscan={{ main: 500, reverse: 500 }}
				itemContent={(index: number) => {
					const group = filteredGroups[index];

					return (
						<LogItemGroup key={group.uuid} {...group} />
					);
				}}
				components={{ ScrollSeekPlaceholder: Placeholder }}
				scrollSeekConfiguration={{
					enter: (velocity, range) =>
						range.endIndex - range.startIndex > 5 && Math.abs(velocity) > 1400,
					exit: (velocity) => Math.abs(velocity) < 400,
				}}
				followOutput="smooth"
			/>

			{!scrollOnLog && newItems && (
				<button
					className="
                                z-10 absolute bottom-5 right-5 flex items-center rounded-full py-1 px-3 shadow-lg transition
                                text-white text-sm bg-blue-500 hover:bg-blue-600
                                dark:text-gray-200 dark:bg-blue-600 dark:hover:bg-blue-700

                            "
					onClick={onClickScrollButton}
				>
					<i className="h-3">
						<DownArrowIcon />
					</i>
					<span className="ml-2 pb-0.5 select-none">new log(s)</span>
				</button>
			)}
			{showResetFilters && (
				<div className="absolute inset-0 overflow-y-auto flex justify-center items-center py-10">
                    <span>
                        {groups.size > 1 ? `${groups.size} items` : '1 item'} filtered out.{' '}
                        <button
                            className="underline hover:text-gray-600"
                            onClick={reset}
                        >
                            Clear filters
                        </button>
                    </span>
				</div>
			)}
		</div>
	);
}
