import React from 'react';
import { ResponseLogItem } from '/shared/types';
import SymfonyData from '@/pages/main/components/SymfonyData';

export default React.memo(function Response({ item }: { item: ResponseLogItem }) {

    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            <dl className="markup-dl">
                <dt>Status code</dt>
                <dd>
                    <SymfonyData id={item.id} content={item.status_code} as="code" />
                </dd>

                {item.json && (
                    <>
                        <dt>JSON</dt>
                        <dd>
                            <SymfonyData
                                id={item.id}
                                content={item.json}
                                className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}
                                expand={item.expand}
                            />
                        </dd>
                    </>
                )}
                {item.headers && (
                    <>
                        <dt>Headers</dt>
                        <dd>
                            <SymfonyData
                                id={item.id}
                                content={item.headers}
                                className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}
                            />
                        </dd>
                    </>
                )}
            </dl>
        </div>
    );
});
