export const isDev = process.env.NODE_ENV === 'development';

export type NotEmptyArray<T> = [T, ...T[]];
export function isNotEmpty<T>(array: Array<T>): array is [T, ...T[]] {
	return array.length > 0;
}

export type Editor =
	| 'Atom'
	| 'Emacs'
	| 'Idea'
	| 'MacVim'
	| 'PhpStorm'
	| 'Sublime'
	| 'TextMate'
	| 'VSCode'
	| 'VSCode-insiders'
	| 'Custom URL';

const platform = (function(){
	if (typeof window === 'undefined') return process.platform;
	return window.Ray.platform;
})();

export const editorOptions: { [K in Editor]: string } = {
	Atom: 'atom://core/open/file?filename=%path&line=%line',
	Emacs: 'emacs://open?url=file://%path&line=%line',
	Idea: 'idea://open?file=%path&line=%line',
	MacVim: 'mvim://open/?url=file://%path&line=%line',
	PhpStorm: 'phpstorm://open?file=%path&line=%line',
	Sublime: platform === 'linux' ? 'subl://%path:%line' : 'subl://open?url=file://%path&line=%line',
	TextMate: 'txmt://open?url=file://%path&line=%line',
	VSCode: 'vscode://file/%path:%line',
	'VSCode-insiders': 'vscode-insiders://file/%path:%line',
	'Custom URL': '',
};