import { useSyncExternalStore } from 'react';
import { IpcRendererEvent } from 'electron';
import { License } from '/shared/types';

let license: License = {
	is_overlay_locked: false,
};

let listeners = new Set<() => void>();

const licenseStore = {
	subscribe(onStoreChange: () => void) {
		listeners.add(onStoreChange);

		return () => {
			listeners.delete(onStoreChange);
		};
	},
	get() {
		return license;
	},
	async init() {
		window.Ray.onLicenseChanged((_: IpcRendererEvent, value: License) => {
			license = value;
			emitChange();
		});

		license = await window.Ray.getLicense();
		emitChange();
	}
};

licenseStore.init();

function emitChange() {
	listeners.forEach(listener => listener());
}

export default function useLicense(): { isValid: boolean, openWindow: () => void, decouple?: () => Promise<void> | void } & License {
	const license = useSyncExternalStore(licenseStore.subscribe, licenseStore.get);

	if (!license?.licensed_to) {
		return {
			isValid: false,
			...license,
			openWindow: window.Ray.openLicenseWindow,
		}
	}

	return {
		isValid: true,
		...license,
		openWindow: window.Ray.openLicenseWindow,
		decouple: () => {
			if (!confirm('Are you sure you want to decouple your license from this device?')) {
				return;
			}

			return window.Ray.decoupleLicense();
		}
	}
}