import React, { Suspense, lazy, useEffect } from 'react';
import Main, { Header as MainHeader } from './pages/main';
import './index.scss';
import styles from './Screen.module.scss';
import Header from '@/components/header/Header';
import usePreferences from '@/stores/preferences';
import { getPage, isDarwin } from '@/utils/util';
import useLicense from '@/stores/license';
import { flare } from '@flareapp/flare-client';


const pageName = getPage();

const main = {
	Main,
	Header: MainHeader,
};

const component = {
	main,
	preferences: {
		Main: lazy(() => import('./pages/preferences')),
		Header: lazy(() => import('./pages/preferences').then(module => ({ default: module.Header }))),
	},
	license: {
		Main: lazy(() => import('./pages/license')),
		Header: lazy(() => import('./pages/license').then(module => ({ default: module.Header }))),
	},
	servers: {
		Main: lazy(() => import('./pages/servers')),
		Header: lazy(() => import('./pages/servers').then(module => ({ default: module.Header }))),
	},
}[pageName || 'main'] ?? main;

export default function App() {
	const { colorblindMode } = usePreferences();
	const license = useLicense();

	useEffect(() => {
		if (!license) return;
		if (!license.licensed_to) return;

		flare.addContextGroup('user', {
			id: license.licensed_to,
			name: license.licensed_to,
			license_key: license.license_key,
			licensed_to: license.licensed_to,
		});
	}, [license]);

	return (
		<div className={styles.Screen} data-colorblind-mode={colorblindMode} data-is-darwin={isDarwin}>
			<Header>
				<Suspense fallback={
					<span className="text-sm font-bold absolute inset-0 flex justify-center items-center text-center capitalize">
						{pageName}
					</span>
				}>
					<component.Header />
				</Suspense>
			</Header>
			<Suspense>
				<component.Main />
			</Suspense>
		</div>
	);
}
