import styles from './ColorFilter.module.scss';
import { useClearColorFilter, useColorFilter } from '@/stores/filter';
import { TimesIcon } from '@/components/Icons';

const FILTER_COLORS = ['green', 'orange', 'red', 'purple', 'blue', 'gray'];
export default function ColorFilter() {
	return (
		<>
			{FILTER_COLORS.map((color) => (
				<ColorBullet
					key={color}
					color={color}
				/>
			))}
			<ClearButton />
		</>
	)
}

function ColorBullet({ color }: { color: string }) {
	const { active, onToggle } = useColorFilter(color);

	return (
		<button
			role="switch"
			aria-checked={active}
			title={(active ? 'Hide ' : 'Show ') + color}
			className={styles.ColorBullet}
			onClick={onToggle}
			data-color={color}
		>
			{color.slice(0, 3)}<span className={styles.lastLetters}>{color.slice(3)}</span>
		</button>
	)
}

function ClearButton() {
	const { active, clear } = useClearColorFilter();

	return (
		<button
			title="Clear filters"
			className={`
            absolute h-5 right-3 sm:right-2
            flex items-center
            
            enabled:opacity-100 enabled:scale-100
            disabled:opacity-0 disabled:scale-90
            
            ml-0.5 text-gray-400 hover:text-gray-500 dark:text-gray-500 dark:hover:text-gray-400 text-base leading-none
            transition-all duration-75
            focus:outline-none focus:ring-0
        `}
			onClick={clear}
			disabled={!active}
		>
                <span className="hidden xs:inline-block w-2 h-3 sm:w-3 sm:h-3">
                    <TimesIcon />
                </span>
		</button>
	)
}
