import { useSyncExternalStore } from 'react';
import { IpcRendererEvent } from 'electron';

const usageStore = {
	isFirstTimeUser: false,
	subscribe(onStoreChange: () => void) {
		usageStore.listeners.add(onStoreChange);
		return () => usageStore.listeners.delete(onStoreChange);
	},
	get() {
		return usageStore.isFirstTimeUser;
	},
	async init() {
		const isFirstTimeUser = await window.Ray.getIsFirstTimeUser();
		if (!isFirstTimeUser) return;

		usageStore.isFirstTimeUser = isFirstTimeUser;
		usageStore.emitChange();
	},
	listeners: new Set<() => void>(),
	emitChange() {
		usageStore.listeners.forEach(listener => listener());
	}
};

usageStore.init();

export function useUsage() {
	const isFirstTimeUser = useSyncExternalStore(usageStore.subscribe, usageStore.get);

	return {
		isFirstTimeUser,
	}
}