import React from 'react';
import { ApplicationLogLogItem } from '/shared/types';
import { stringifyItemContent } from '@/utils/util';
import SymfonyData from '@/pages/main/components/SymfonyData';

export default React.memo(function ApplicationLog({ item }: { item: ApplicationLogLogItem; }) {

    return (
        <>
            <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
                <embedded-webview html={stringifyItemContent(item.value)} />
            </div>
            {item.context && (
                <SymfonyData
                    id={item.id}
                    content={item.context}
                    className={`mt-2 w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}
                    expand={item.expand}
                />
            )}
        </>
    );
});
