import { Frame } from '/shared/types';

export function getFrameLabel(frame: Frame): string {
	let label = frame.class || '';

	if (frame.method === 'require' && frame.class === null) {
		return `${frame.file_name}:${frame.line_number}`;
	}

	if (frame.class) {
		label += ':';
	}

	label += frame.method;

	return label;
}

