import React from 'react';
import { EloquentModelLogItem } from '/shared/types';
import SymfonyData from '@/pages/main/components/SymfonyData';


export default React.memo(function Model({ item }: { item: EloquentModelLogItem }) {
    const ref = React.useRef<HTMLDivElement>(null);

    return (
        <div ref={ref} className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            {item.class_name ? (
                <dl className="markup-dl markup-dl-collapse">
                    <dt>Class Name</dt>
                    <dd>
                        <SymfonyData id={item.id} content={item.class_name} as="code" />
                    </dd>

                    <dt>Attributes</dt>
                    <SymfonyData id={item.id} content={item.attributes} as="dd" />
                    {item.relations && (
                        <>
                            <dt>Relations</dt>
                            <SymfonyData id={item.id} content={item.relations} as="dd" />
                        </>
                    )}
                </dl>
            ) : (
                <>No model found</>
            )}
        </div>
    );
});
