import { useCallback, useSyncExternalStore } from 'react';
import type { Preferences } from '/shared/types';
import { IpcRendererEvent } from 'electron';

let preferences: Preferences = {
	port: '23517',
	floating: false,
	displayTimes: true,
	scrollOnLog: true,
	editor: 'PhpStorm',
	customEditor: '',
	colorblindMode: 'Disabled',
	toggleVisibilityGlobalHotkey: 'CmdOrCtrl+Shift+L',
	newScreenGlobalHotkey: 'CmdOrCtrl+Shift+K',
	toggleAlwaysOnTopHotkey: '',
	newWindow: 'server',
	appVisibility: 'both',
	theme: 'automatic',
	servers: [],
	historyLimit: 100000,
};

let listeners = new Set<() => void>();

const preferencesStore = {
	subscribe(onStoreChange: () => void) {
		listeners.add(onStoreChange);

		return () => {
			listeners.delete(onStoreChange);
		};
	},
	get() {
		return preferences;
	},
	async init() {
		window.Ray.onPreferencesChanged((_: IpcRendererEvent, value: Preferences) => {
			preferences = value;
			emitChange();
		});
		preferences = await window.Ray.getPreferences();
		emitChange();
	}
};

preferencesStore.init();

function emitChange() {
	listeners.forEach(listener => listener());
}

export default function usePreferences() {
	return useSyncExternalStore(preferencesStore.subscribe, preferencesStore.get);
}

function setPreference<F extends keyof Preferences>(field: F, value: typeof preferences[F]) {
	const setter = window.Ray.setPreference[field];

	if (!setter) throw new Error(`Unknown preference field: ${field}`);
	setter(value);
}

export function usePreferencesState(): [
	Preferences,
	<T extends keyof Preferences, V extends Preferences[T]>(field: T, value: V) => void
] {
	const preferences = usePreferences();

	return [preferences, setPreference];
}

export function useFloating(): [boolean, (value: boolean) => void] {
	const floating = useSyncExternalStore(preferencesStore.subscribe, () => preferences.floating);

	return [floating, (value: boolean) => setPreference('floating', value)];
}

export function usePreference<F extends keyof Preferences>(field: F): [Preferences[F], (value: Preferences[F]) => void] {
	const value = useSyncExternalStore(preferencesStore.subscribe, () => preferences[field]);
	const setter = useCallback((value: Preferences[F]) => {
		setPreference(field, value)
	}, [field, setPreference]);

	return [
		value,
		setter
	];
}