import { CSSProperties, PropsWithChildren } from 'react';
import styles from './Tag.module.scss';

type Props = PropsWithChildren<{
	variant?: 'outline' | 'filled';
	color?: string;
	className?: string;
	style?: CSSProperties;
}>;
export default function Tag({ variant = 'filled', children, color, className = '', style = {} }: Props) {
	return (
		<div
			className={`${styles.Tag} ${className}`}
			data-variant={variant}
			style={style}
			data-color={color}
		>
			{children}
		</div>
	);
}

export function TagOutline(props: Omit<Props, 'variant'>) {
	return (
		<Tag variant={'outline'} {...props} />
	);
}

Tag.Outline = TagOutline;