import { Group } from '/shared/types';
import { useRef, useState, useEffect } from 'react';
import { PlusIcon, MinIcon } from '@/components/Icons';
import usePreferences from '@/stores/preferences';
import Item from './Item';
import Separator from './renderers/Separator';
import styles from './Logs.module.scss';

export default function LogItemGroup({ items, uuid, color, collapsed }: Group) {
	const { displayTimes } = usePreferences();
	const { contentRef, maxHeight } = useHeight(collapsed);


	if (items[0].type === 'separator') {
		return <Separator groupColor={color} />;
	}

	function setIsCollapsed(isCollapsed: boolean) {
		window.Ray.collapsed.set({ id: uuid, isCollapsed });
	}

	function toggleCollapsed() {
		window.Ray.collapsed.toggle({ id: uuid });
	}

	return (
		<div
			className={styles.Group}
			onClick={() => {
				if (collapsed) {
					setIsCollapsed(false);
				}
			}}
			data-color={color}
		>
			<div
				ref={contentRef}
				style={{ maxHeight }}
				aria-expanded={!collapsed}
				className={styles.logItem}
			>
				<div className="relative grid sm:grid-cols-auto-1fr items-stretch">
					<nav
						className={`
                            relative h-full
                            px-2 py-4
                            ${
							displayTimes &&
							'md:bg-gray-100 md:bg-opacity-25 md:dark:bg-black md:dark:bg-opacity-25'
						}
                            sm:block
                            hidden
                        `}
						onClick={() => setIsCollapsed(!collapsed)}
					>
						<button
							title={!displayTimes ? items[0].time : ''}
							className={styles.collapse}
						>
							<div>
								{collapsed ? <PlusIcon /> : <MinIcon />}
							</div>
						</button>
					</nav>


					<div className="relative sm:pr-3.5 sm:pl-0 min-w-0 overflow-hidden">
						{items.map((item, i) => (
							<Item
								item={item}
								key={item.id}
								first={i === 0}
								groupColor={color}
								collapse={toggleCollapsed}
							/>
						))}
					</div>
				</div>
			</div>
		</div>
	);
}

function useHeight(isCollapsed: boolean, minHeight = 48) {
	const contentRef = useRef<HTMLDivElement>(null);

	const [maxHeight, setMaxHeight] = useState<number | 'none'>(isCollapsed ? minHeight : 'none');

	const lastIsCollapsed = useRef(isCollapsed);

	useEffect(() => {
		let timeouts: Array<NodeJS.Timeout> = [];
		if (lastIsCollapsed.current && !isCollapsed) {
			setMaxHeight(contentRef.current?.scrollHeight || 'none');

			timeouts.push(setTimeout(() => setMaxHeight('none'), 200));
		}

		if (!lastIsCollapsed.current && isCollapsed) {
			if (maxHeight === 'none') {
				setMaxHeight(contentRef.current?.scrollHeight || 'none');
				timeouts.push(setTimeout(() => setMaxHeight(minHeight), 50));
			} else {
				setMaxHeight(minHeight);
			}
		}

		lastIsCollapsed.current = isCollapsed;

		return () => timeouts.forEach((timeout) => clearTimeout(timeout));
	}, [isCollapsed]);

	return { contentRef, isCollapsed, maxHeight };
}