import React from 'react';
import EditorUrl from '@/components/ui/EditorUrl';
import { CallerLogItem, Frame } from '/shared/types';

export default React.memo(function Trace({ item }: { item: CallerLogItem }) {
    function label(frame: Frame): string {
        let label = frame.class;

        if (frame.class) {
            label += ':';
        }

        label += frame.method;

        return label;
    }

    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            <div className={``}>
                Called from
                <EditorUrl
                    path={item.frame.file_name}
                    fileName={item.frame.file_name}
                    lineNumber={item.frame.line_number}
                    label={label(item.frame)}
                    small={false}
                />
            </div>
        </div>
    );
});
