import React, { memo } from 'react';
import { LogLogItem } from '/shared/types';
import SymfonyData from '@/pages/main/components/SymfonyData';

export default memo(function Log({ item }: { item: LogLogItem }) {

    const usesDumpScript = item.content.match(/(sf-dump-[0-9]+)/);

    if (usesDumpScript) {
        return (
            <SymfonyData
                id={item.id}
                content={item.content}
                className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`} whitespace-pre-wrap pb-4`}
                clipboardData={item.meta?.clipboard_data}
                expand={item.expand}
            />
        )
    }

    return (
        <div
            className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`} whitespace-pre-wrap`}
        >
            {item.content}
        </div>
    );
});
