import React from 'react';
import { CopyButton } from '@/components/ui/CopyButton';
import { ExpandData } from '/shared/types';

export default function Data({ content, as, className = '', clipboardData, expand }: { id: number, content: any, as?: 'dd' | 'code', className?: string, clipboardData?: string, expand?: ExpandData }) {
	const Component = as ?? 'div';

	if (content === undefined) {
		return (
			<Component className={className}>
				undefined
			</Component>
		)
	}

	if (typeof content === 'string') {
		const usesDumpScript = content.match(/(sf-dump-[0-9]+)/);

		return (
			<Component
				className={`${className} relative`}
			>
				{usesDumpScript ? <symfony-dump content={content} expandLevel={expand?.level} expandPaths={expand?.keys.map(key => key?.replaceAll(',', '\\,'))} key={usesDumpScript[0]} /> : <embedded-webview html={content} />}
				{clipboardData && (
					<div className="absolute top-1 right-1 sm:top-2 sm:right-2 flex items-center opacity-50 hover:opacity-100 transition-opacity">
						<CopyButton text={clipboardData} />
					</div>
				)}
			</Component>
		)

	}

	return (
		<Component className={className}>
			{JSON.stringify(content, null, 4)}
		</Component>
	);
}