import { DetailedHTMLProps, HTMLAttributes } from 'react';
import DOMPurify from 'dompurify';

class EmbeddedWebview extends HTMLElement {
    connectedCallback() {
        const shadow = this.attachShadow({ mode: 'closed' });
        const dirtyHTML = this.getAttribute('html')!;
        const cleanHTML = DOMPurify.sanitize(dirtyHTML);
        shadow.innerHTML = cleanHTML;
    }
}

export default function registerMailWebComponent() {
    if (window.customElements.get('embedded-webview')) {
        return;
    }

    document.addEventListener('navigateEvent', (e: any) => window.Ray.openUrl(e.detail.url));
    window.customElements.define('embedded-webview', EmbeddedWebview);
}

declare global {
    namespace JSX {
        interface IntrinsicElements {
            'embedded-webview': DetailedHTMLProps<
                HTMLAttributes<HTMLElement> & { html: string },
                HTMLElement
            >;
        }
    }
}
