export function stringifyItemContent(content: any) {
    if (content === undefined) {
        return 'undefined';
    }

    if (typeof content === 'string') {
        return content;
    }

    return JSON.stringify(content, null, 4);
}

function getUrlParameter(param: string) {
    return new URL(document.URL).searchParams.get(param);
}

export function getPage() {
    return getUrlParameter('page');
}

export function getProjectName() {
    return getUrlParameter('projectName');
}

export function getRemoteServerLabel() {
    return getUrlParameter('remoteServerLabel');
}

export const isDarwin = window.Ray.platform === 'darwin';

/**
 * Gives all visitable sub paths on a given set of paths.
 * ['array.1', 'array.2', 'array.3'] => ['array', 'array.1', 'array.2', 'array.3']
 */

export function getUniqueNodesFromPaths(paths: Array<string>){
    return Array.from(paths.reduce((subPaths, path) => {
        const keys = escapedSplit(path, '.');
        for(let i = 1; i < keys.length; i++) {
            subPaths.add(keys.slice(0, i).join('.'));
        }
        return subPaths;
    }, new Set(paths)));
}

const tempNonBreakingSeparator = '__§NONBREAKINGSEPARATOR§__';
export function escapedSplit(string: string, separator = '.', escapeCharacter = '\\') {
    return string
        .replaceAll(`${escapeCharacter}${separator}`, tempNonBreakingSeparator)
        .split(separator)
        .map(s => s.replaceAll(tempNonBreakingSeparator, `${escapeCharacter}${separator}`));
}

