import React, { useMemo } from 'react';
import CopyButton from '@/components/ui/CopyButton';
import queryParser from './queryParser';
import { LogItem } from '/shared/types';

type Props = {
    item: LogItem & { type: 'executed_query' };
};

export default React.memo(function Query({ item }: Props) {

    const { query, comments } = useMemo(() => queryParser(item), [item.content, item.bindings]);

    return (
        <div className="max-w-full">
            <div className="relative">
                <pre
                    className={`scrollbar-small overflow-x-auto code ${
                        item.style.text ? `markup-${item.style.text}` : `markup`
                    }`}
                >
                    <code className="language-sql">
                        {query}
                    </code>
                </pre>

                <CopyButton className="absolute top-0 right-0 w-5 h-5 m-3" text={query} />
            </div>
            {Object.keys(comments).length > 0 && (
                <dl className="mt-3 mr-3 markup-dl">
                    {Object.entries(comments).map(([key, value]) => (
                        <React.Fragment key={key}>
                            <dt className="text-xs">{key}</dt>
                            <dd className="text-xs">{value}</dd>
                        </React.Fragment>
                    ))}
                </dl>
            )}
            <dl className="mt-3 markup-dl text-xs">
                {item.connectionName !== '' && (
                    <>
                        <dt>Connection name</dt>
                        <dd>{item.connectionName}</dd>
                    </>
                )}

                {item.duration && (
                    <>
                        <dt>Time</dt>
                        <dd>{item.duration} ms</dd>
                    </>
                )}
            </dl>
        </div>
    );
});
