import React, { useCallback, useMemo, useState } from 'react';
import EditorUrl from '@/components/ui/EditorUrl';
import FlareShareButton from '@/components/ui/FlareShareButton';
import hljs from 'highlight.js/lib/core';
import { ExceptionLogItem, Frame } from '/shared/types';
import { EditIcon, MinIcon, PlusIcon } from '@/components/Icons';
import Collapsible from '@/components/ui/Collapsible';
import { getFrameLabel } from '@/utils/frame';

function useRememberCollapsedState<T>(initialState: T, key: string): [T, React.Dispatch<React.SetStateAction<T>>] {
    const [value, _setValue] = useState<T>((window.REMEMBERED_COLLAPSED_STATES.get(key) as T) ?? initialState);

    const setValue = useCallback((newValue: React.SetStateAction<T>) => {
        if (typeof newValue === 'function') {
            _setValue((currentValue) => {
                const result = (newValue as Function)(currentValue);
                window.REMEMBERED_COLLAPSED_STATES.set(key, result);
                return result;
            });
        } else {
            _setValue(newValue);
            window.REMEMBERED_COLLAPSED_STATES.set(key, newValue);
        }
    }, [key]);

    return [value, setValue];
}

export default React.memo(function Exception({ item }: { item: ExceptionLogItem }) {
    const [openFrames, setOpenFrames] = useRememberCollapsedState<Set<number>>(new Set([0]), `${item.groupUuid}-${item.id}-openFrames`);
    const [isExpanded, setIsExpanded] = useRememberCollapsedState(false, `${item.groupUuid}-${item.id}-isExpanded`);


    const report = item.meta?.flare_report;
    const flareResponse = item.meta?.flare_response;

    return (
        <>
            <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
                <div className="flex items-start justify-between">
                    <dl className="markup-dl">
                        <dt>Message</dt>
                        <dd
                            className={`font-semibold min-w-0 cursor-pointer ${isExpanded ? '' : 'truncate'}`}
                            title={item.message}
                            onClick={() => setIsExpanded(!isExpanded)}
                        >
                            {item.message}
                        </dd>

                        <dt>Type</dt>
                        <dd>
                            <code>{item.class}</code>
                        </dd>
                    </dl>
                    <span className="hidden sm:block flex-none py-1">
                        <FlareShareButton wrap report={report} flareResponse={flareResponse} id={item.id} groupUuid={item.groupUuid} />
                    </span>
                </div>
                <div className="my-4 grid grid-cols-1 gap-2">
                    {item.frames.map((frame: Frame, index) => (
                        <ErrorFrame
                            frame={frame}
                            index={index}
                            key={index}
                            isOpen={openFrames.has(index)}
                            toggleOpen={() => {
                                if (openFrames.has(index)) {
                                    openFrames.delete(index);
                                } else {
                                    openFrames.add(index);
                                }
                                setOpenFrames(new Set(openFrames));
                            }}
                            idPrefix={`${item.id}`}
                        />
                    ))}
                </div>
            </div>
            <div className="my-4 sm:hidden">
                <FlareShareButton report={report} flareResponse={flareResponse} id={item.id} groupUuid={item.groupUuid} />
            </div>
        </>
    );
});

function ErrorFrame({ index, frame, isOpen, idPrefix, toggleOpen }: { index: number, frame: Frame, isOpen: boolean, idPrefix: string, toggleOpen: () => void }) {
    return (
        <div aria-expanded={isOpen}>
            <CollapsibleHeader controls={`${idPrefix}-frame-${index}`} frame={frame} isOpen={isOpen} toggle={toggleOpen} />
            <Collapsible
                collapsed={!isOpen}
                id={`${idPrefix}-frame-${index}`}
            >
                <div className="sm:ml-6 mb-4 mt-2
                    flex
                    bg-gray-100 bg-opacity-25 dark:bg-gray-900 dark:bg-opacity-25
                    rounded-sm
                    border border-gray-200 dark:border-gray-800
                ">
                    <CodeBlock frame={frame} />
                </div>
            </Collapsible>
        </div>
    );
}

function LineNumbers({ lines, highlight }: { lines: number[], highlight: number }) {
    return (
        <div className="
            grid justify-items-end
            px-1 py-2
            bg-gray-100 bg-opacity-50 dark:bg-gray-900 dark:bg-opacity-50
            border-r border-gray-200 dark:border-gray-800
            text-gray-500
            line-numbers
        ">
            {lines.map((lineNumber, i) => (
                <div
                    className={`sm:py-1
                        ${lineNumber === highlight
                            ? 'font-bold text-black dark:text-white'
                            : ''
                        }`
                    }
                    key={i}
                >
                    <code>{lineNumber}</code>
                </div>
            ))}
        </div>
    );
}
function CodeBlock({ frame }: { frame: Frame }) {
    const minLeadingSpaces = Math.min(
        ...frame
            .snippet!.filter((line) => line.text !== '')
            .map((line) => {
                return line.text.search(/\S|$/);
            })
    );
    const lines = frame.snippet!.map((line) => {
        return {
            ...line,
            text: line.text.slice(minLeadingSpaces),
        };
    });

    const highlightedLines = useMemo(() => {
        const language = frame.file_name.endsWith('.blade.php') ? 'blade' :  'php';
        const block = lines.map(snippetLine => snippetLine.text).join('\n');
        const highlightedBlock = hljs.highlight(block, { language });

        return highlightedBlock.value.split('\n');
    }, [frame]);

    return (
        <>
            <LineNumbers
                lines={frame.snippet!.map(line => line.line_number)}
                highlight={frame.line_number}
            />
            <pre className="py-2 grid w-full overflow-x-scroll scrollbar-small">
                {lines.map((snippetLine, i) => (
                    <div
                        className={`
                            flex items-center group relative pr-2 sm:py-1
                            ${
                                frame.line_number === snippetLine.line_number
                                    ? 'bg-purple-100 dark:bg-purple-900 dark:bg-opacity-50'
                                    : 'hover:bg-purple-100 hover:bg-opacity-25 dark:hover:bg-purple-900 dark:hover:bg-opacity-25'
                            }
                        `}
                        key={snippetLine.line_number}
                    >
                        <span className="cursor-pointer z-10 sticky left-0 px-1">
                            <EditorUrl
                                path={frame.file_name}
                                fileName={frame.file_name}
                                lineNumber={snippetLine.line_number}
                                label={
                                    <span
                                        className="
                                            flex-none flex items-center justify-center
                                            w-4 h-4 sm:w-5 sm:h-5 rounded-full
                                            focus:outline-none focus:ring-0
                                            opacity-0 group-hover:opacity-100
                                            bg-purple-500
                                        "
                                    >
                                        <div title="Edit" className="w-1.5 h-1.5 text-white">
                                            <EditIcon />
                                        </div>
                                    </span>
                                }
                            />
                        </span>
                        <span
                            dangerouslySetInnerHTML={{
                                __html: highlightedLines[i] || ' ',
                            }}
                        />
                    </div>
                ))}
            </pre>
        </>
    );
}

type CollapsibleHeaderProps = {
    frame: Frame;
    isOpen: boolean;
    toggle: () => void;
    controls: string;
};

function CollapsibleHeader({ frame, isOpen, toggle, controls }: CollapsibleHeaderProps) {
    return (
        <div className="group flex space-x-2 items-center justify-start" onClick={toggle} role="button" aria-expanded={isOpen} aria-controls={controls} id={`${controls}-trigger`}>
            <span
                className="
                    flex-none flex items-center justify-center
                    w-4 h-4 rounded-full
                    focus:outline-none focus:ring-0
                    transform scale-50 group-hover:scale-100
                    transition-transform hover:duration-75 duration-150
                    bg-purple-500 text-white
                "
            >
                <div
                    className="
                        w-2 h-2
                        opacity-0 group-hover:opacity-100
                        transition-opacity hover:duration-75 duration-150
                    "
                >
                    {isOpen ? <MinIcon /> : <PlusIcon />}
                </div>
            </span>
            <strong className="min-w-0 truncate cursor-pointer font-semibold">{getFrameLabel(frame)}</strong>
            <span className="flex-none">
                <EditorUrl
                    path={frame.file_name}
                    fileName={frame.file_name}
                    lineNumber={frame.line_number}
                    label={
                        <div className="opacity-0 group-hover:opacity-100 text-gray-500 hover:text-indigo-500 transition-opacity duration-150">
                            Edit
                        </div>
                    }
                />
            </span>
        </div>
    );
}
