import React from 'react';
import { JsonLogItem } from '/shared/types';
import SymfonyData from '@/pages/main/components/SymfonyData';


export default React.memo(function Json({ item }: { item: JsonLogItem }) {
    return (
        <div className="relative">
            <SymfonyData
                id={item.id}
                content={item.value}
                className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}
                expand={item.expand}
            />

        </div>
    );
});
