import { useMemo, useState } from 'react';
import { ToggleButton } from './Button';
import { FolderTreeIcon } from '@/components/Icons';
import { useScreen } from '@/stores/screen';
import DropdownMenu from './DropdownMenu';
import { LogItem, UniqueCodePath } from '/shared/types';

export default function FileList() {
	const { groups } = useScreen();
	const files = useMemo(() => getUniqueCodePaths(Array.from(groups.values()).flatMap(group => group.items)), [groups]);

	const [isShowingFiles, setIsShowingFiles] = useState(false);

	return (
		<ToggleButton
			disabled={groups.size === 0}
			active={isShowingFiles}
			title="Show files"
			onClick={() => setIsShowingFiles(!isShowingFiles)}
		>
			<FolderTreeIcon />
			{isShowingFiles && (
				<DropdownMenu
					inset="left"
					options={files.map((file) => ({
						label: file.lineNumbers.length === 1 ? `${file.fileName}:${file.lineNumbers[0]}` : file.fileName,
						onClick: () => window.Ray.openEditor({ path: file.path, lineNumber: file.lineNumbers[0] }),
						subLabels:
							file.lineNumbers.length === 1
								? undefined
								: file.lineNumbers.map((line) => ({
									text: line.toString(),
									onClick: () => window.Ray.openEditor({ path: file.path, lineNumber: line }),
								})),
					}))}
					close={() => setIsShowingFiles(false)}
				/>
			)}
		</ToggleButton>
	)
}

export function getUniqueCodePaths(logItems: Array<LogItem>): Array<UniqueCodePath> {
	return logItems.reduce((uniqueCodePaths, logItem) => {
		const existingPathIndex = uniqueCodePaths.findIndex((path) => path.path === logItem.path);

		if (existingPathIndex === -1) {
			uniqueCodePaths.push({
				lineNumbers: [logItem.lineNumber],
				path: logItem.path,
				fileName: logItem.fileName,
			});

			return uniqueCodePaths;
		}

		if (!uniqueCodePaths[existingPathIndex].lineNumbers.includes(logItem.lineNumber)) {
			uniqueCodePaths[existingPathIndex].lineNumbers.push(logItem.lineNumber);
		}

		return uniqueCodePaths;
	}, [] as Array<UniqueCodePath>);
}