import { DetailedHTMLProps, HTMLAttributes } from 'react';
import DOMPurify from 'dompurify';
import { escapedSplit, getUniqueNodesFromPaths, isDarwin } from '@/utils/util';
import symfonyDumpStyles from '@/symfony-dump.scss?inline';

const pathKeyClasses = '.sf-dump-index, .sf-dump-key, .sf-dump-protected, .sf-dump-public, .sf-dump-private, .sf-dump-meta';
class SymfonyDump extends HTMLElement {
	toggledStateChanged = false;
	scriptId = '';
	samples: HTMLElement[] = [];

	connectedCallback() {
		const shadow = this.attachShadow({ mode: 'open' });
		const dirtyHTML = this.getAttribute('content')!;
		if (!dirtyHTML) return;

		const usesDumpScript = dirtyHTML.match(/(sf-dump-[0-9]+)/);
		if (!usesDumpScript) return;
		this.scriptId = usesDumpScript[0];

		const cleanHTML = DOMPurify.sanitize(dirtyHTML, { RETURN_DOM_FRAGMENT: true });
		Array.from(cleanHTML.querySelectorAll(pathKeyClasses)).forEach(el => el.setAttribute('data-key', el.textContent!));
		this.samples = Array.from(cleanHTML.querySelectorAll('samp'));

		this.samples.forEach(sample => {
			const arrow = document.createElement('button');
			arrow.className = 'sf-dump-ref sf-dump-toggle';
			arrow.title = `[${isDarwin ? 'Cmd' : 'Ctrl'} click] Expand all children`;
			arrow.innerHTML = '▶';
			arrow.onclick = (e: MouseEvent) => {
				this.toggledStateChanged = true;
				const opening = sample.classList.contains('sf-dump-compact');
				if (e.metaKey || e.ctrlKey) {
					const children = Array.from(sample.querySelectorAll('samp'));
					children.forEach(child => {
						if (opening) {
							child.classList.remove('sf-dump-compact');
							child.classList.add('sf-dump-expanded');
						} else {
							child.classList.add('sf-dump-compact');
							child.classList.remove('sf-dump-expanded');
						}
					})
				}
				sample.classList.toggle('sf-dump-compact');
				sample.classList.toggle('sf-dump-expanded');
			}
			sample.parentNode?.insertBefore(arrow, sample);
		});

		/*
		* TODO: goto reference
		*  a.sf-dump-ref has a href like #sf-dump-2046920694-ref22
		*  there might be a samp with id sf-dump-2046920694-ref22
		*  if there is, open it (recursively upwards) and scroll to it
		* */

		if (window.SF_DUMP_STATES.has(this.scriptId)) {
			const toggledState = window.SF_DUMP_STATES.get(this.scriptId)!;

			this.samples.forEach((sample, index) => {
				const shouldToggle = sample.classList.contains('sf-dump-compact') !== toggledState[index];
				if (!shouldToggle) return;

				sample.classList.toggle('sf-dump-compact');
				sample.classList.toggle('sf-dump-expanded');
			});
		} else {
			const expandLevel = this.getAttribute('expandLevel');
			if (expandLevel) this.expandLevel(expandLevel);

			const expandPaths = this.getAttribute('expandPaths');
			if (expandPaths) this.expandPaths(expandPaths, cleanHTML);
		}

		const styles = document.createElement('style');
		styles.innerHTML = symfonyDumpStyles;
		shadow.append(styles, cleanHTML);
	}
	disconnectedCallback() {
		if (!this.toggledStateChanged) return;

		const samples = Array.from(this.shadowRoot?.querySelectorAll('samp') ?? []);
		const toggledState = samples.map(sample => sample.classList.contains('sf-dump-compact'))
		window.SF_DUMP_STATES.set(this.scriptId, toggledState);
	}

	static get observedAttributes() {
		return ['expandlevel', 'expandpaths'];
	}
	attributeChangedCallback(name: 'expandlevel' | 'expandpaths') {
		if (!this.scriptId) return;
		if (name === 'expandlevel') return;

		// reset to default
		this.samples.forEach(sample => {
			const isRoot = sample.getAttribute('data-depth') === '1';
			const currentCollapsed = sample.classList.contains('sf-dump-compact');
			const shouldToggle = isRoot === currentCollapsed;

			if (!shouldToggle) return;

			sample.classList.toggle('sf-dump-compact');
			sample.classList.toggle('sf-dump-expanded');
		});

		const expandLevel = this.getAttribute('expandLevel');
		if (expandLevel !== null) this.expandLevel(expandLevel);

		const expandPaths = this.getAttribute('expandPaths');
		if (expandPaths) this.expandPaths(expandPaths, this.shadowRoot);
	}

	private expandLevel(levelString: string) {
		this.toggledStateChanged = false;
		window.SF_DUMP_STATES.delete(this.scriptId);

		const level = parseInt(levelString);
		this.samples.forEach(sample => {
			const dataDepth = sample.getAttribute('data-depth');
			const shouldBeExpanded = parseInt(dataDepth ?? '9999999999') <= level;
			const currentCollapsed = sample.classList.contains('sf-dump-compact');
			const shouldToggle = currentCollapsed === shouldBeExpanded;

			if (!shouldToggle) return;

			sample.classList.toggle('sf-dump-compact');
			sample.classList.toggle('sf-dump-expanded');
		});
	}

	private expandPaths(pathsString: string | null, doc: DocumentFragment | null) {
		this.toggledStateChanged = false;
		window.SF_DUMP_STATES.delete(this.scriptId);

		if (!pathsString || !doc) return;
		/*
		* path sections are separated by a dot
		* sections are textContent of .sf-dump-key or .sf-dump-index sibling before [data-depth] samp that should be expanded
		* Earlier we moved the textContent of those elements to data-key attribute so that we can use pure css selectors
		* multiple sections can exist in one level
		* we want to expand the entire parent path of an element
		* TODO: wildcard * can be used to expand all children of a section
		* */
		const paths = pathsString ? escapedSplit(pathsString, ',') : [];
		const pathsWithParents = getUniqueNodesFromPaths(paths);

		if (!pathsWithParents.length) return;

		const root = this.samples[0];
		const isRootCollapsed = root.classList.contains('sf-dump-compact');
		if (isRootCollapsed) {
			root.classList.toggle('sf-dump-compact');
			root.classList.toggle('sf-dump-expanded');
		}

		pathsWithParents.forEach(path => {
			const keys = escapedSplit(path, '.');
			const selector = keys.reduce((selector, key) =>
					`${selector} > [data-key="${key.replaceAll('\\.', '.').replaceAll('\\', '\\\\')}"] ~ :is(samp, ${pathKeyClasses})` // pathKeyClasses matches when a key doesn't have a collapsible samp
				, 'pre > samp'
			);
			let sample;
			try {
				sample = doc.querySelector(selector);
			} catch (e) {
				console.error(e);
				console.warn('invalid selector: ', selector)
			}

			if (!sample) return;
			const currentCollapsed = sample.classList.contains('sf-dump-compact');
			if (!currentCollapsed) return;

			sample.classList.toggle('sf-dump-compact');
			sample.classList.toggle('sf-dump-expanded');
		});
	}
}

export default function registerSymfonyDumpComponent() {
	if (window.customElements.get('symfony-dump')) {
		return;
	}

	document.addEventListener('navigateEvent', (e: any) => window.Ray.openUrl(e.detail.url));
	window.customElements.define('symfony-dump', SymfonyDump);
}

declare global {
	namespace JSX {
		interface IntrinsicElements {
			'symfony-dump': DetailedHTMLProps<
				HTMLAttributes<HTMLElement> & { content: string, expandLevel?: number | null, expandPaths?: string[] },
				HTMLElement
			>;
		}
	}
}
