import { PropsWithChildren } from 'react';
import styles from './Button.module.scss';

type ButtonProps = PropsWithChildren<{
	disabled?: boolean;
	title?: string;
	onClick?: () => void;
	onRightClick?: () => void;
}>;

export function Button({ onRightClick, ...props }: ButtonProps) {
	return (
		<button
			onContextMenu={onRightClick}
			className={styles.Button}
			{...props}
		/>
	)
}

export function ToggleButton({ active, onRightClick, ...props }: ButtonProps & { active?: boolean }) {
	return (
		<button
			onContextMenu={onRightClick}
			className={styles.Button}
			aria-checked={active}
			role="switch"
			{...props}
		/>
	)
}

