import React from 'react';
import { ViewLogItem } from '/shared/types';
import EditorUrl from '@/components/ui/EditorUrl';
import SymfonyData from '@/pages/main/components/SymfonyData';


export default React.memo(function View({ item }: { item: ViewLogItem }) {

    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            <dl className="markup-dl">
                <dt>Path</dt>
                <dd>
                    <EditorUrl fileName={item.view_path_relative_to_project_root} path={item.view_path} />
                </dd>

                <dt>Data</dt>
                <SymfonyData id={item.id} content={item.data} as="dd" expand={item.expand} />
            </dl>
        </div>
    );
});
