import { ToggleButton } from './Button';
import { ServerIcon } from '@/components/Icons';
import { getRemoteServerLabel } from '@/utils/util';

export default function ServerButton() {
	const connectedToServer = Boolean(getRemoteServerLabel());

	return (
		<ToggleButton active={connectedToServer} onClick={window.Ray.openServers}>
			<ServerIcon />
		</ToggleButton>
	);
}

