import { ReactNode } from 'react';
import usePreferences from '@/stores/preferences';
import { editorOptions } from '/shared/utils';


export function useHasEditor() {
    const { editor, customEditor } = usePreferences();

    if (!editor) return false;
    if (editor === 'Custom URL') return Boolean(customEditor);
    return Boolean(editorOptions[editor])
}

type Props = {
    path: string;
    fileName: string;
    lineNumber?: number;
    label?: ReactNode;
    small?: boolean;
    className?: string;
    truncateStart?: boolean;
};

export default function EditorUrl({
    path,
    fileName,
    lineNumber,
    label,
    small = true,
    className,
    truncateStart = false,
}: Props) {
    const hasEditor = useHasEditor();

    if (!label) {
        label = fileName;

        if (lineNumber) {
            label = `${fileName}:${lineNumber}`;
        }
    }

    if (small !== false) {
        small = true;
    }

    return (
        <button
            className={`
                    block truncate min-w-0 leading-none
                    ${truncateStart ? 'rtl align-left' : ''}
                    ${hasEditor ? 'underline' : 'cursor-default'}
                    ${
                        small
                            ? 'text-gray-500 text-xs hover:text-gray-600  dark:hover:text-gray-400'
                            : 'hover:text-gray-900 dark:hover:text-gray-200'
                    }
                    ${className}
                `}
            onClick={(e) => {
                e.stopPropagation();
                window.Ray.openEditor({ path, lineNumber });
            }}
            title={`${path}:${lineNumber}`}
        >
            {label}
        </button>
    );
}
