import React from 'react';
import { MeasureLogItem } from '/shared/types';

type Props = {
    item: MeasureLogItem;
};

export default React.memo(function Measure({ item }: Props) {
    return (
        <div>
            {item.is_new_timer ? (
                <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
                    Start measuring performance...
                </div>
            ) : (
                <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
                    <dl className="markup-dl">
                        <dt>Total time</dt>
                        <dd>{formatTime(item.total_time)}</dd>

                        <dt>Maximum memory usage</dt>
                        <dd>{formatFilesize(item.max_memory_usage_during_total_time)}</dd>

                        <dt>Time since last call</dt>
                        <dd>{formatTime(item.time_since_last_call)}</dd>
                    </dl>
                </div>
            )}
        </div>
    );
});

function formatTime(time: number) {
    if (time >= 1000) {
        return (time / 1000).toFixed(3) + ' s';
    }

    return time + ' ms';
}

function formatFilesize(filesize: number) {
    if (filesize >= 1073741824) {
        return (filesize / 1024 / 1024 / 1024).toFixed(2) + ' GB';
    }

    if (filesize >= 1048576) {
        return (filesize / 1024 / 1024).toFixed(2) + ' MB';
    }

    return (filesize / 1024).toFixed(2) + ' KB';
}
