import { useSyncExternalStore } from 'react';
import { IpcRendererEvent } from 'electron';

let isPaused = false;

let listeners = new Set<() => void>();

export const pausedStore = {
	setIsPaused(value: boolean) {
		if (isPaused === value) return;
		window.Ray.setIsPaused(value);
	},
	subscribe(onStoreChange: () => void) {
		listeners.add(onStoreChange);

		return () => {
			listeners.delete(onStoreChange);
		};
	},
	getIsPaused() {
		return isPaused;
	},
	async init() {
		window.Ray.onPausedChanged((_: IpcRendererEvent, value: boolean) => {
			if (isPaused === value) return;

			isPaused = value;
			emitChange();
		});


		const value = await window.Ray.getIsPaused();

		if (isPaused === value) return;

		isPaused = value;
		emitChange();
	}
};

pausedStore.init();

export default function usePausedState(): [boolean, (value: boolean) => void] {
	return [
		useSyncExternalStore(pausedStore.subscribe, pausedStore.getIsPaused),
		pausedStore.setIsPaused
	];
}

function emitChange() {
	listeners.forEach(listener => listener());
}