import React, { PropsWithChildren, useEffect, useState } from 'react';

export default function Collapsible({ collapsed, children, id }: PropsWithChildren<{ collapsed: boolean, id: string }>) {
	const [contentHidden, setContentHidden] = useState(collapsed);

	useEffect(() => {
		if (!collapsed) {
			setContentHidden(false);
		}
	}, [collapsed]);

	return (
		<div
			className={`grid ${
				collapsed ? 'grid-rows-[0fr]' : 'grid-rows-[1fr]'
			} transform grid-template-rows-transform duration-200 ease-in`}
			id={id}
			role="region"
			aria-labelledby={`${id}-trigger`}
			onTransitionEnd={(e) => {
				if (collapsed) {
					setContentHidden(true);
				}
			}}
		>
			<div className="overflow-hidden">{!contentHidden && children}</div>
		</div>
	);
}