import { useCallback, useEffect, useRef, useState } from 'react';
import type { Lock } from '/shared/types';


export default function useLock(lockName: string): [Lock | undefined, (stopExecution?: boolean) => void] {
	const [lock, setLock] = useState<Lock | undefined>();
	const once = useRef(false);
	useEffect(() => {
		if (once.current) return;
		once.current = true;

		window.Ray.lock.get(lockName).then(lock => {
			setLock(lock)
		});
	}, [lockName])

	const liftLock = useCallback((stopExecution = false) => {
		window.Ray.lock.deactivate({ lockName, stopExecution });
		setLock((lock) => ({
			...lock!,
			active: false,
			stop_execution: stopExecution
		}))
	}, [lockName])


	return [lock, liftLock];
}