import React, { useState } from 'react';
import { CopyButton } from '@/components/ui/CopyButton';
import CodeBlock from '@/components/ui/codeBlocks/CodeBlock';
import { MailableLogItem, Person } from '/shared/types';


export default React.memo(function Mailable({ item }: { item: MailableLogItem }) {
    const [viewSource, setViewSource] = useState(false);

    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            <dl className="markup-dl mb-3 w-full">
                {item.from.length > 0 && (
                    <>
                        <dt>From</dt>
                        <dd>{convertPersons(item.from)}</dd>
                    </>
                )}

                {item.subject?.length && (
                    <>
                        <dt>Subject</dt>
                        <dd>{item.subject}</dd>
                    </>
                )}

                {item.to.length > 0 && (
                    <>
                        <dt>To</dt>
                        <dd>{convertPersons(item.to)}</dd>
                    </>
                )}

                {item.cc.length > 0 && (
                    <>
                        <dt>Cc</dt>
                        <dd>{convertPersons(item.cc)}</dd>
                    </>
                )}

                {item.bcc.length > 0 && (
                    <>
                        <dt>Bcc</dt>
                        <dd>{convertPersons(item.bcc)}</dd>
                    </>
                )}
                {item.mailable_class && (
                    <>
                        <dt>Mailable class</dt>
                        <dd>
                            <code>{item.mailable_class}</code>
                        </dd>
                    </>
                )}
            </dl>
            <div className="relative">
                <embedded-webview html={item.html} />
                {viewSource && <CodeBlock className="absolute inset-0" language="xml" code={item.html} />}
                <div className="absolute top-1 right-1 sm:top-2 sm:right-2 flex items-center opacity-50 hover:opacity-100 transition-opacity">
                    <Button onClick={() => setViewSource(!viewSource)}>{viewSource ? 'rendered' : 'source'}</Button>
                    <CopyButton text={item.html} position="end" />
                </div>
            </div>
        </div>
    );
});

function Button(props: { onClick?: () => void; children: React.ReactNode }) {
    return (
        <button
            className="h-6 py-0.5 px-2 rounded-l
                flex items-center
                text-white
                focus:outline-none focus:ring-0
                bg-indigo-500 hover:bg-indigo-600
                transform active:translate-y-px
                cursor-default select-none app-region-no-drag
                border-r border-indigo-600
                "
            {...props}
        />
    );
}

function convertPersons(persons: Array<Person>) {
    return (
        <>
            {persons.map((person, i) => (
                <React.Fragment key={i}>
                    {person.email} {person.name !== '' ?? `(${person.name})`}
                </React.Fragment>
            ))}
        </>
    );
}
