import React from 'react';
import { LockLogItem } from '/shared/types';
import { PlayIcon, StopIcon } from '@/components/Icons';
import useLock from '@/stores/lock';

export type Lock = {
    name: string;
    active: boolean;
    stop_execution: boolean;
    displayed_on_group_uuid: string;
};

export default React.memo(function Lock({ item }: { item: LockLogItem }) {
    const [lock, liftLock] = useLock(item.lockName || '');

    const hasActiveLock = lock ? lock.active : false;

    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            {hasActiveLock ? (
                <div className="-mt-1 inline-grid grid-flow-col items-center">
                    <button
                        className="flex items-center space-x-2
                                            px-4 py-1
                                            bg-gray-100 dark:bg-black dark:bg-opacity-20 rounded-l-full
                                            focus:ring-0 focus:outline-none
                        "
                        onClick={() => liftLock()}
                    >
                        <span className="w-2 h-auto text-green-500">
                            <PlayIcon />
                        </span>
                        <span>Continue</span>
                    </button>

                    <button
                        className="
                            flex items-center space-x-2
                            px-4 py-1
                            border-l border-gray-300 dark:border-gray-800
                            bg-gray-100 dark:bg-black dark:bg-opacity-20 rounded-r-full
                            focus:ring-0 focus:outline-none
                        "
                        onClick={() => liftLock(true)}
                    >
                        <span className="w-2 h-auto text-red-500">
                            <StopIcon />
                        </span>
                        <span>
                            Stop <span className="hidden md:inline">execution</span>
                        </span>
                    </button>
                </div>
            ) : (
                <div className="text-gray-400 flex items-center space-x-2">
                    {lock?.stop_execution ? (
                        <>
                            <span className="w-2 h-auto text-gray-300">
                                <StopIcon />
                            </span>
                            <span>Stopping execution…</span>
                        </>
                    ) : (
                        <>
                            <span className="w-2 h-auto text-gray-300">
                                <PlayIcon />
                            </span>
                            <span>Continuing execution…</span>
                        </>
                    )}
                </div>
            )}
        </div>
    );
});
