import { useFloating } from '@/stores/preferences';
import { ToggleButton } from './Button';
import { FloatIcon } from '@/components/Icons';

export default function FloatButton() {
	const [isFloating, setIsFloating] = useFloating();

	return (
		<ToggleButton active={isFloating} onClick={() => setIsFloating(!isFloating)}>
			<FloatIcon />
		</ToggleButton>
	)
}


