import { format } from 'sql-formatter';
import { flare } from '@flareapp/flare-client';

const parseSqlComment = (rawSql: string) => {
    try {

        const trimmedSql = rawSql.trim().replace(/;\s*$/, '');
        const start = trimmedSql.lastIndexOf('/*');
        const end = trimmedSql.lastIndexOf('*/');
        if (start > end || end !== trimmedSql.length - 2) return { query: rawSql, comments: {} };

        const query = trimmedSql.substring(0, start);
        const rawComment = trimmedSql.substring(start + 2, end);

        const comments = Object.fromEntries(
            rawComment.split(',').map((kv) => {
                const [rawKey, rawValue] = kv.split('=');
                const key = decodeURIComponent(rawKey);
                const value = decodeURIComponent(rawValue.slice(1, -1));
                return [key, value];
            })
        );

        return { query, comments };
    } catch (e) {
        flare.report(e as Error, { values: { rawSql }})
        return { query: rawSql, comments: {} };
    }
};

function formatParams(params: any): any {
    // recursively handle arrays of parameters
    if (Array.isArray(params)) {
        return params.map((param) => formatParams(param));
    }

    // recursively handle named parameter objects
    if (params && typeof params === 'object') {
        let formattedParams: Record<string, any> = {};
        Object.entries(params).forEach(([key, value]) => {
            formattedParams[key] = formatParams(value);
        });
        return formattedParams;
    }

    // quote string parameters
    if (typeof params === 'string') {
        return `'${params.replace(/'/g, "''")}'`;
    }

    if (typeof params === 'number') {
        return `${params}`;
    }
    // return other parameter types as-is
    return params;
}

const queryParser = ({ content, bindings }: { content?: string; bindings: any }) => {
    if (!content) return { query: '', comments: {} };

    const { query, comments } = parseSqlComment(content);

    const dialects = ['mysql', 'postgresql', 'sql', 'sqlite'];

    for (const dialect of dialects) {
        try {
            const formattedQuery = format(query, { params: formatParams(bindings), language: dialect });
            return {
                query: formattedQuery,
                comments,
            };
        } catch (e) {}
    }
    return {
        query: query,
        comments,
    };
};

export default queryParser;
