import React, { useMemo } from 'react';
import CopyButton from '@/components/ui/CopyButton';
import { JsonStringLogItem } from '/shared/types';
import { ObjectInspector } from 'react-inspector';
import useTheme from '@/components/ui/codeBlocks/useTheme';
import { flare } from '@flareapp/flare-client';
import { getUniqueNodesFromPaths } from '@/utils/util';

export default React.memo(function JsonString({ item }: {  item: JsonStringLogItem }) {
    const theme = useTheme();

    const data = useMemo(() => {
        try {
            return JSON.parse(item.value)
        } catch (e) {
            flare.report(e as SyntaxError);

            return item.value;
        }
    }, [item.value]);

    const expandPaths = useMemo(() => {
        if (!item.expand?.keys.length) return;

        const prefixedKeys = item.expand.keys.map((key) => `$.${key}`);
        return getUniqueNodesFromPaths(prefixedKeys);
    }, [item.expand]);

    return (
        <div className="bg-gray-300 dark:bg-gray-800 bg-opacity-25 sm:p-3 p-2 text-xs sm:text-sm">
            <ObjectInspector
                data={data}
                // @ts-ignore
                theme={theme}
                expandLevel={item.expand ? item.expand.level : 4}
                expandPaths={expandPaths}
            />

            <CopyButton text={item.value} className="absolute top-0 right-0 sm:w-5 sm:h-5 sm:m-3 w-4 h-4 m-2" />
        </div>
    );
});
