import { Screen } from '/shared/types';
import { useSyncExternalStore } from 'react';
import { flare } from '@flareapp/flare-client';

let screen: Screen = {
	groups: new Map(),
	name: '',
};

const meta = {
	activeIndex: 0,
	amountOfScreens: 1
}

let listeners = new Set<() => void>();


window.Ray.onNewScreenData((event, value) => {
	if (!value.screen) {
		flare.report(new Error('No screen data received. This is a bug.'), {
			previous: meta,
			next: {
				activeIndex: value.activeIndex,
				amountOfScreens: value.amountOfScreens
			}
		});
	}
	screen = value.screen ?? { groups: new Map(), name: '' };
	meta.activeIndex = value.activeIndex;
	meta.amountOfScreens = value.amountOfScreens;
	emitChange();
});

const screenStore = {
	subscribe(onStoreChange: () => void) {
		listeners.add(onStoreChange);

		return () => {
			listeners.delete(onStoreChange);
		};
	},
	getSnapshot() {
		return screen;
	}
};

const metaStore = {
	subscribe(onStoreChange: () => void) {
		listeners.add(onStoreChange);

		return () => {
			listeners.delete(onStoreChange);
		};
	},
	getSnapshot() {
		return meta;
	}
};

function emitChange() {
	listeners.forEach(listener => listener());
}

export function useScreen() {
	return useSyncExternalStore(screenStore.subscribe, screenStore.getSnapshot);
}

export function useHistory() {
	const screen = useScreen();
	const { activeIndex, amountOfScreens } = useSyncExternalStore(metaStore.subscribe, metaStore.getSnapshot)

	return {
		hasPrevious: activeIndex > 0,
		hasNext: activeIndex < amountOfScreens - 1,
		hasItems: screen.groups.size > 0,
		index: activeIndex,
		setIndex: window.Ray.setScreenIndex,
		createNew: window.Ray.newScreen
	}
}