import { useMemo, useSyncExternalStore } from 'react';

let screens: Array<{ index: number, name?: string }> = [];

let listeners = new Set<() => void>();


const screensStore = {
	subscribe(onStoreChange: () => void) {
		listeners.add(onStoreChange);

		return () => {
			listeners.delete(onStoreChange);
		};
	},
	getSnapshot() {
		return screens;
	},
	async init() {
		window.Ray.onScreensUpdated((_, value) => {
			screens = value;
		});

		screens = await window.Ray.getScreens();

		emitChange();
	}
};

screensStore.init();

function emitChange() {
	listeners.forEach(listener => listener());
}

export function useScreens(from: number, to?: number) {
	const screens = useSyncExternalStore(screensStore.subscribe, screensStore.getSnapshot);
	return useMemo(() => {
		return screens.slice(from, to);
	}, [screens, from, to]);
}