import React, { useEffect } from 'react';
import { DropdownMenuOption } from '/shared/types';

type Props = {
    options: Array<DropdownMenuOption>;
    inset?: 'left' | 'right';
    close: () => void;
};

export default function DropdownMenu({ options, inset = 'left', close }: Props) {
    useEffect(() => {
        setTimeout(() => {
            document.addEventListener('click', close);
        });

        return () => document.removeEventListener('click', close);
    }, []);

    return (
        <div
            style={{ maxHeight: 'calc( 100vh - 8rem)' }}
            className={`
                z-40 absolute top-full max-h-screen overflow-auto
                ${inset === 'left' ? 'left-0' : 'right-0'}
                mt-2 py-5 px-6
                bg-white dark:bg-gray-800 rounded shadow-xl border border-gray-300 dark:border-gray-700
            `}
            onClick={(e) => e.stopPropagation()}
        >
            {options.length > 0 && (
                <ul className="grid gap-3">
                    {options.map((option, i) => (
                        <li key={i}>
                            <a
                                className={`
                                    text-xs text-gray-500 hover:text-gray-600 dark:text-gray-400 dark:hover:text-gray-300
                                    ${option.onClick ? 'underline  cursor-pointer' : ''}
                                `}
                                onClick={option.onClick}
                            >
                                {option.label}
                            </a>

                            {option.subLabels && (
                                <div className="text-gray-500 whitespace-normal">
                                    {option.subLabels.map((subLabel, i) => (
                                        <React.Fragment key={i}>
                                            <span
                                                className={`
                                                    text-xs text-gray-500 hover:text-gray-600 dark:text-gray-300 dark:hover:text-gray-200 opacity-75
                                                    ${subLabel.onClick ? 'underline  cursor-pointer' : ''}
                                                `}
                                                onClick={subLabel.onClick}
                                            >
                                                {subLabel.text}
                                            </span>
                                            {i < option.subLabels!.length - 1 && ', '}
                                        </React.Fragment>
                                    ))}
                                </div>
                            )}
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
}
