import React, { useState } from 'react';
import CopyButton from './CopyButton';

export type FlareResponse = {
    public_url: string;
    owner_url: string;
};

type Props = {
    report: any;
    wrap?: boolean;
    flareResponse?: FlareResponse;
    id: number,
    groupUuid: string,
};

export default function FlareShareButton({ report, wrap = false, flareResponse, id, groupUuid }: Props) {

    const [isLoading, setIsLoading] = useState(false);

    async function sendReportToFlare() {
        setIsLoading(true);
        await window.Ray.sendReportToFlare({ report, id, groupUuid });
        setIsLoading(false);
    }

    if (!report) {
        return null;
    }

    return (
        <div
            className="
            text-xs markup-text-xs 
            text-purple-500 dark:text-purple-400"
        >
            {flareResponse ? (
                <div className="flex space-x-4">
                    <ShareUrl url={flareResponse.public_url}>Public share</ShareUrl>
                    <ShareUrl url={flareResponse.owner_url}>Share admin</ShareUrl>
                </div>
            ) : (
                <div className={`${wrap ? 'flex-col items-end' : 'items-center'} flex`}>
                    <button
                        className={`
                            px-3 py-0.5 -my-0.5
                            font-semibold rounded-full
                            focus:ring-0 focus:outline-none
                            ${isLoading && 'opacity-50'} 
                            bg-purple-100 hover:bg-purple-200 dark:bg-purple-900 dark:bg-opacity-50 dark:hover:bg-opacity-100
                        `}
                        style={{ cursor: 'pointer !important' }}
                        disabled={isLoading}
                        onClick={sendReportToFlare}
                    >
                        <span>{isLoading ? 'Loading…' : 'Share exception'}</span>
                    </button>
                    <div className="py-1 text-xxs markup-text-xxs opacity-75">
                        &nbsp;via&nbsp;
                        <a
                            className="underline hover:text-purple-600 dark:hover:text-purple-300"
                            href="https://flareapp.io/docs/ignition-for-laravel/sharing-errors"
                        >
                            Flare
                        </a>
                    </div>
                </div>
            )}
        </div>
    );
}

type ShareUrlProps = {
    url: string;
    children: string;
};

function ShareUrl({ url, children }: ShareUrlProps) {
    return (
        <div className="flex items-center">
            <a className="underline text-xs hover:text-purple-600 dark:hover:text-purple-300" href={url}>
                {children}
            </a>
            <CopyButton text={url} textColor="text-purple-500 dark:text-purple-400" className="ml-1 h-3 w-3" />
        </div>
    );
}
