import styles from './Main.module.scss';
import React, { useEffect, useMemo } from 'react';
import { useScreen } from '@/stores/screen';
import usePausedState from '@/stores/paused';
import LogTable from './components/logs/LogTable';
import registerHighlightJs from '@/utils/registerHighlightJs';
import registerMailWebComponent from '@/utils/registerMailWebComponent';
import useEffects from '@/effects/useEffects';
import HistoryButtons from './components/header/History';
import FileList from './components/header/FileList';
import PauseButton from './components/header/PauseButton';
import ColorFilter from './components/header/ColorFilter';
import Search from './components/header/Search';
import FloatButton from './components/header/FloatButton';
import ServerButton from './components/header/ServerButton';
import registerSymfonyDumpComponent from '@/utils/registerSymfonyWebComponent';
import useLicense from '@/stores/license';
import Instructions from '@/pages/main/components/instructions/Instructions';
import { getProjectName, getRemoteServerLabel } from '@/utils/util';

export default function Main() {
	useEffects();
	const screen = useScreen();

	useEffect(() => {
		registerHighlightJs();
		registerMailWebComponent();
		registerSymfonyDumpComponent();
	}, []);

	const screenName = useMemo(() => [getRemoteServerLabel(), getProjectName(), screen.name].filter(Boolean).join(' - '), [screen.name]);

	return (
		<>
			{screenName && (<div className={styles.name} data-color={screen.color}>{screenName}</div>)}
			<main>
				<PauseBanner />
				<LicenseBanner />
				{screen.groups.size > 0 && (
					<LogTable groups={screen.groups} />
				)}
				{screen.groups.size === 0 && <Instructions />}

			</main>
		</>
	)
}

export function Header() {
	return (
		<>
			<div>
				<HistoryButtons />
				<FileList />
				<PauseButton />
			</div>
			<div className="relative xs:px-6">
				<ColorFilter />
			</div>
			<div>
				<Search />
				<FloatButton />
				<ServerButton />
			</div>
		</>
	);
}


function PauseBanner() {
	const [isPaused, setIsPaused] = usePausedState();

	if (!isPaused) return null;

	return (
		<button
			className={styles.PauseBanner}
			onClick={() => setIsPaused(false)}
		>
			Ray will ignore all new messages until you <u>unpause</u>
		</button>
	);
}

function differenceInDays(a: Date, b: Date) {
	const _MS_PER_DAY = 1000 * 60 * 60 * 24;
	// Discard the time and time-zone information.
	const utc1 = Date.UTC(a.getFullYear(), a.getMonth(), a.getDate());
	const utc2 = Date.UTC(b.getFullYear(), b.getMonth(), b.getDate());

	return Math.floor((utc2 - utc1) / _MS_PER_DAY);
}

function LicenseBanner() {
	const { openWindow, end_of_grace_period, has_active_license, amount_of_requests } = useLicense();

	if (has_active_license) {
		return null;
	}

	const gracePeriodEndsInDays = end_of_grace_period ? differenceInDays(new Date(), new Date(end_of_grace_period)) : null;

	if (gracePeriodEndsInDays !== null && gracePeriodEndsInDays > 5) {
		return null;
	}

	if (gracePeriodEndsInDays) {
		return (
			<button
				className={styles.LicenseBanner}
				onClick={openWindow}
			>
				<span>
                    We were not able to check your license with the server. Ray will go into demo mode in {gracePeriodEndsInDays} day(s).
                </span>
			</button>
		)
	}


	return (
		<button
			className={styles.LicenseBanner}
			onClick={openWindow}
		>
			<span>
                Trial mode allows for up to 10 log items. (currently {amount_of_requests! > 10 ? 10 : amount_of_requests}
				/10)
                <u className="hover:text-yellow-500 underline ml-2">
                    Learn more…
                </u>
            </span>

		</button>
	);
}





