import React from 'react';
import { CustomLogItem } from '/shared/types';
import SymfonyData from '@/pages/main/components/SymfonyData';

export default React.memo(function Custom({ item }: { item: CustomLogItem }) {

    return (
        <SymfonyData
            id={item.id}
            content={item.content}
            className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}
            expand={item.expand}
        />
    );
});
