import React from 'react';
import { CarbonLogItem, LogItem } from '/shared/types';

export default React.memo(function Carbon({ item }: { item: CarbonLogItem }) {
    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            {item.formatted ? (
                <dl className="markup-dl">
                    <dt>Formatted</dt>
                    <dd>
                        <code>{item.formatted}</code>
                    </dd>

                    <dt>Timezone</dt>
                    <dd>
                        <code>{item.timezone}</code>
                    </dd>

                    <dt>Timestamp</dt>
                    <dd>
                        <code>{item.timestamp}</code>
                    </dd>
                </dl>
            ) : (
                <code>null</code>
            )}
        </div>
    );
});
