import React from 'react';
import { JobEventLogItem } from '/shared/types';
import SymfonyData from '@/pages/main/components/SymfonyData';


export default React.memo(function Job({ item }: { item: JobEventLogItem }) {
    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            <dl className="markup-dl">
                <dt>Job event</dt>
                <dd>
                    <code>{item.event_name}</code>
                </dd>
                <dt>Job</dt>
                <dd>
                    <SymfonyData id={item.id} content={item.job} expand={item.expand} />
                </dd>

                {item.exception && item.exception.length > 0 && (
                    <>
                        <dt>Exception</dt>
                        <dd>
                            <SymfonyData id={item.id} content={item.exception} />
                        </dd>
                    </>
                )}
            </dl>
        </div>
    );
});
