import { useCallback, useSyncExternalStore } from 'react';
import { IpcRendererEvent } from 'electron';

const searchStore = {
	state: {
		query: '',
		active: false,
	},
	subscribe(onStoreChange: () => void) {
		searchStore.listeners.add(onStoreChange);
		return () => searchStore.listeners.delete(onStoreChange);
	},
	get() {
		return searchStore.state;
	},
	toggle() {
		searchStore.state = {
			query: searchStore.state.active ? '' : searchStore.state.query,
			active: !searchStore.state.active
		}

		searchStore.emitChange();
	},
	setQuery(query: string) {
		searchStore.state = {
			query: query.toLowerCase(),
			active: searchStore.state.active
		};
		searchStore.emitChange()
	},
	async init() {
		window.Ray.onToggleSearch((_: IpcRendererEvent) => {
			searchStore.toggle();
		});
	},
	listeners: new Set<() => void>(),
	emitChange() {
		searchStore.listeners.forEach(listener => listener());
	}
};

searchStore.init();

export function useSearch() {
	const { query, active } = useSyncExternalStore(searchStore.subscribe, searchStore.get);

	return {
		active,
		toggle: searchStore.toggle,
		query,
		setQuery: searchStore.setQuery,
	}
}

export const COLORS = {
	green: 1,
	orange: 2,
	red: 4,
	purple: 8,
	blue: 16,
	gray: 32,
} as Record<string, number>;

const colorFilterStore = {
	activeFilters: 0,

	get(color: string) {
		return Boolean(colorFilterStore.activeFilters & COLORS[color]);
	},
	getActiveFilters() {
		return colorFilterStore.activeFilters;
	},
	getActive() {
		return Boolean(colorFilterStore.activeFilters);
	},
	toggle(color: string) {
		colorFilterStore.activeFilters = colorFilterStore.activeFilters ^ COLORS[color];
		colorFilterStore.emitColorChange(color);
		colorFilterStore.emitChange();
	},
	clear() {

		const activeFilters = Object.entries(COLORS).filter(([, value]) => colorFilterStore.activeFilters & value);
		colorFilterStore.activeFilters = 0;
		activeFilters.forEach(([color]) => colorFilterStore.emitColorChange(color));
		colorFilterStore.emitChange();
	},
	subscribe(onStoreChange: () => void) {
		colorFilterStore.listeners.add(onStoreChange);
		return () => colorFilterStore.listeners.delete(onStoreChange);
	},
	subscribeColor(color: string) {
		return function (onStoreChange: () => void) {
			if (!colorFilterStore.colorListeners.has(color)) {
				colorFilterStore.colorListeners.set(color, new Set([onStoreChange]));
			} else {
				colorFilterStore.colorListeners.get(color)!.add(onStoreChange);
			}

			return () => colorFilterStore.colorListeners.get(color)!.delete(onStoreChange);
		}
	},

	colorListeners: new Map<string, Set<() => void>>(),
	listeners: new Set<() => void>(),

	emitColorChange(color: string) {
		colorFilterStore.colorListeners.get(color)!.forEach(listener => listener());
	},
	emitChange() {
		colorFilterStore.listeners.forEach(listener => listener());
	}
};

export function useClearColorFilter() {
	const active = useSyncExternalStore(colorFilterStore.subscribe, colorFilterStore.getActive);

	return {
		active,
		clear: () => colorFilterStore.clear()
	}
}
export function useColorFilter(color: string) {
	const active = useSyncExternalStore(colorFilterStore.subscribeColor(color), () => colorFilterStore.get(color));
	return {
		active: active,
		onToggle: () => colorFilterStore.toggle(color)
	}
}

export function useFilters() {
	const { query } = useSyncExternalStore(searchStore.subscribe, searchStore.get);
	const colorFilters = useSyncExternalStore(colorFilterStore.subscribe, colorFilterStore.getActiveFilters);

	const reset = useCallback(() => {
		if (query) searchStore.toggle();
		colorFilterStore.clear();
	}, []);

	return {
		query,
		colorFilters,
		reset
	};
}

