import React, { useMemo } from 'react';
import hljs from 'highlight.js/lib/core';
import { format, Options } from 'prettier';
import parserBabel from 'prettier/parser-babel';
import parserHtml from 'prettier/parser-html';
import parseCss from 'prettier/parser-postcss';

type Props = React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLElement> & { language: string; code: string },
    HTMLElement
>;

const options: Record<'js' | 'xml' | string, Options> = {
    js: { parser: 'babel', plugins: [parserBabel] },
    xml: { parser: 'html', plugins: [parserHtml, parseCss] },
};

export default function CodeBlock({ code, language, className = '', ...props }: Props) {
    const __html = useMemo(() => {
        const formattedCode = options[language] ? format(code, options[language]) : code;
        return hljs.highlight(formattedCode, { language: language }).value || '';
    }, [code, language]);
    return (
        <code className={`bg-gray-100 dark:bg-gray-800 px-4 py-2 overflow-auto ${className}`} {...props}>
            <pre dangerouslySetInnerHTML={{ __html }} />
        </code>
    );
}

