import React, { useCallback, useEffect, useState } from 'react';
import { CheckIcon, CopyIcon } from '@/components/Icons';

type CopyButtonProps = {
	text: string;
	textColor?: string;
	className?: React.HTMLAttributes<HTMLDivElement>['className'];
};

export function useCopy() {
	const [copied, setCopied] = useState(false);

	useEffect(() => {
		let timeout: number;
		if (copied) {
			timeout = setTimeout(() => {
				setCopied(false);
			}, 3000) as any;
		}

		return () => {
			if (timeout) {
				clearTimeout(timeout);
			}
		};
	}, [copied]);

	const handleCopy = useCallback((text: string) => {
		navigator.clipboard.writeText(text)
			.then(() => setCopied(true));
	}, []);

	return {
		copied,
		handleCopy,
	};
}

export default function Copy({ text, textColor = 'text-gray-500 dark:text-gray-300', className }: CopyButtonProps) {
	const { copied, handleCopy } = useCopy();

	return (
		<div
			className={`
                cursor-pointer opacity-50 hover:opacity-75
                ${className}
                ${copied ? 'text-green-500' : textColor}
            `}
			onClick={() => handleCopy(text)}
		>
			{copied ? <CheckIcon /> : <CopyIcon />}
		</div>
	);
}

export function CopyButton({ text, position }: { text: string, position?: 'start' | 'middle' | 'end' }) {
	const { copied, handleCopy } = useCopy();

	return (
		<button
			className={`h-6 py-1.5 px-2 rounded
                flex items-center
                text-white
                focus:outline-none focus:ring-0
                bg-indigo-500 hover:bg-indigo-600
                transform active:translate-y-px
                cursor-default select-none app-region-no-drag
                ${copied ? 'text-green-500' : ''}
                ${position === 'end' ? 'rounded-l-none' : ''}
                ${position === 'start' ? 'rounded-r-none' : ''}
                ${position === 'middle' ? 'rounded-none' : ''}
            `}
			onClick={() => handleCopy(text)}
		>
			{copied ? <CheckIcon /> : <CopyIcon />}
		</button>
	);
}
