import React, { PropsWithChildren, ReactNode, useState } from 'react';
import TipsList from './TipsList.mdx';
import ExternalLink from '@/components/ui/ExternalLink';
import { shootConfetti } from '@/effects/confetti';
import pkg from '/package.json';

const tipsComponents = {
    code: (props: PropsWithChildren<any>) => (
        <code className="text-sm bg-gray-100 dark:bg-gray-700 px-2 py-0.5 inline-block rounded" {...props} />
    ),
    ul: ({ children, ...props }: PropsWithChildren<any>) => (
        <ul className="grid gap-4 list-disc" {...props}>
            <PickAndShuffle amount={2}>{children as ReactNode[]}</PickAndShuffle>
        </ul>
    ),
    a: (props: PropsWithChildren<any>) => (
        <ExternalLink {...props} />
    ),
    ConfettiButton({ children }: PropsWithChildren) {
        return (
            <button onClick={shootConfetti}>
                {children}
            </button>
        );
    }
};

const appVersion = pkg.version;

export default function Tips() {
    return (
        <div className="grid place-items-center min-h-full">
            <div className="px-8 sm:px-16 py-16 flex flex-col gap-4 select-none overflow-y-auto ">
                <h1 className="markup-h1">💡 Tips</h1>
                <TipsList components={tipsComponents} />
                <p className="mt-16">
                    You can customize shortcuts in{' '}
                    <button
                        className="underline text-indigo-500 hover:text-indigo-600"
                        onClick={() => window.Ray.openPreferencesWindow()}
                    >
                        preferences
                    </button>
                    .
                </p>

                <p>
                    <ExternalLink href="https://spatie.be/docs/ray">
                        Read the docs
                    </ExternalLink> or <ExternalLink href={`https://spatie.be/products/ray/release-notes#${appVersion}`}>
                        {appVersion} release notes
                    </ExternalLink>.
                </p>
            </div>
        </div>
    );
}

function PickAndShuffle({ children, amount }: { amount: number; children: Array<ReactNode> }) {
    const [picked] = useState(() => {
        const activeChildren = children.filter((child) => child !== '\n');
        return shuffle(activeChildren).slice(0, amount);
    });

    return <>{picked}</>;
}

function shuffle<T>(array: Array<T>): Array<T> {
    return array.sort(() => 0.5 - Math.random());
}
