import styles from './Button.module.scss';
import { useState } from 'react';
import { Button } from './Button';
import { AngleLeftIcon, AngleRightIcon, PlusIcon } from '@/components/Icons';
import { useHistory } from '@/stores/screen';
import DropdownMenu from '@/pages/main/components/header/DropdownMenu';
import { useScreens } from '@/stores/screens';

export default function HistoryButtons() {
	const {
		hasPrevious,
		hasNext,
		hasItems,
		index,
		setIndex,
		createNew,
	} = useHistory();
	const [isDropdownOpen, setIsDropdownOpen] = useState<false | 'before' | 'after'>(false);

	return (
		<div className={styles.HistoryButtons}>
			<Button
				disabled={!hasPrevious}
				onClick={() => setIndex(index - 1)}
				title={!hasPrevious ? 'No previous screen' : 'Previous screen'}
				onRightClick={() => hasPrevious ? setIsDropdownOpen('before') : {}}
			>
				<AngleLeftIcon />
			</Button>

			{hasNext ? (
				<Button
					title={'Next screen'}
					onClick={() => setIndex(index + 1)}
					onRightClick={() => setIsDropdownOpen('after')}
				>
					<AngleRightIcon />
				</Button>
			) : (
				<Button
					disabled={!hasItems}
					title={!hasItems ? 'No need to create a new screen, current screen is empty' : 'Add screen'}
					onClick={createNew}
				>
					<PlusIcon />
				</Button>
			)}

			{isDropdownOpen === 'before' && (
				<Popover
					from={0}
					to={index}
					close={() => setIsDropdownOpen(false)}
					setIndex={setIndex}
				/>
			)}

			{isDropdownOpen === 'after' && (
				<Popover
					from={index + 1}
					close={() => setIsDropdownOpen(false)}
					setIndex={setIndex}
				/>
			)}
		</div>
	)
}

function Popover({ from, to, close, setIndex }: { from: number, to?: number, close: () => void, setIndex: (index: number) => void }) {
	const screens = useScreens(from, to);

	return (
		<DropdownMenu
			inset="left"
			options={screens.map(screen => ({
				label: <span className="whitespace-nowrap">{screen.name || `Screen ${screen.index}`}</span>,
				onClick: () => {
					setIndex(screen.index);
					close();
				},
			}))}
			close={close}
		/>
	)
}

