import styles from './Search.module.scss';
import { ToggleButton } from './Button';
import { SearchIcon } from '@/components/Icons';
import { useSearch } from '@/stores/filter';

export default function Search() {
	const { active, toggle, query, setQuery} = useSearch();

	return (
		<div className="relative">
			<ToggleButton active={active} onClick={toggle}>
				<SearchIcon />
			</ToggleButton>
			{active && (
				<input
					className={styles.SearchInput}
					type="search"
					autoFocus
					value={query}
					placeholder="Filter…"
					onChange={e => {
						setQuery(e.target.value)
					}}
				/>
			)}
		</div>
	)
}

