import { CSSProperties } from 'react';
import { LogItem } from '/shared/types';
import Tag from '@/components/ui/Tag';

type Props = {
	itemType: LogItem['type'];
	label?: string;
	start?: boolean;
	stop?: boolean;
	className?: string;
	style?: CSSProperties;
};

const types: { [key in LogItem['type']]: string } = {
	log: '',
	application_log: 'Log',
	executed_query: 'Query',
	trace: 'Trace',
	caller: 'Caller',
	eloquent_model: 'Model',
	mailable: 'Mail',
	lock: '',
	measure: 'Measure',
	event: 'Event',
	job_event: 'Job',
	custom: '',
	json: 'Json',
	json_string: 'Json',
	response: 'Response',
	carbon: 'Carbon',
	view: 'View',
	table: 'Table',
	exception: 'Exception',
	separator: 'Separator',
};

export default function TypeBadge({
	itemType,
	label = types[itemType],
	start = false,
	stop = false,
	className = '',
	style,
}: Props) {
	if (!label) {
		return null;
	}

	return (
		<Tag.Outline
			className={className}
			style={{
				borderBottomStyle: start ? 'dashed' : 'solid',
				borderTopStyle: stop ? 'dashed' : 'solid',
				...style,
			}}
		>
            {label}
        </Tag.Outline>
	);
}
