import { LogItem } from '/shared/types';
import TypeBadge from './TypeBadge';
import LogItemRender from './renderers';
import Tag from '@/components/ui/Tag';
import EditorUrl from '@/components/ui/EditorUrl';
import { ServerIcon } from '@/components/Icons';
import ErrorBoundary from '@/components/ErrorBoundary';
import usePreferences from '@/stores/preferences';

type Props = {
	item: LogItem;
	groupColor: string;
	first: boolean;
	collapse: () => void;
}

export default function Item({ item, groupColor, first, collapse }: Props) {
	const { displayTimes, colorblindMode } = usePreferences();

	const Component = LogItemRender[item.type] ?? (() => null);

	return (
		<div
			className={`
                ${displayTimes && 'md:grid md:grid-cols-log-group md:items-stretch'}
                border-b last:border-b-0 border-dotted border-gray-200 dark:border-gray-800
                px-2 md:px-0 
            `}
		>
			<header
				className={`relative
                    ${
					displayTimes &&
					'md:bg-gray-100/25 md:dark:bg-black/25 md:border-dotted md:border-gray-200 md:dark:border-gray-800 md:border-r'
				}
                `}
				onClick={collapse}
			>
				{(displayTimes || item.type !== 'log' || colorblindMode === 'Enabled') && (
					<div /* [&:not(:empty)]:pt-2 [&:not(:empty)]:sm:pt-4 */
						className={`
                            sticky top-0 
                            flex w-full items-center 
                            pt-2 sm:pt-4
                            space-x-1 
                            ${displayTimes && 'md:flex-col md:items-end md:py-4 md:pr-2 md:space-x-0 md:space-y-1'}
                        `}
					>
						{displayTimes && first && (
							<Tag>
								{item.time}
							</Tag>
						)}

						{/* Subsequent items in a group can stretch width beyond 100% below the color dot */}
						<TypeBadge
							style={{ maxWidth: first ? '100%' : 'calc(100% + 1.25rem)' }}
							className="md:text-right"
							itemType={item.type}
							label={item.label}
							start={item.type === 'measure' && item.is_new_timer}
							stop={item.type === 'measure' && !item.is_new_timer}
						/>

						{colorblindMode === 'Enabled' && (
							<Tag.Outline>{groupColor}</Tag.Outline>
						)}
					</div>
				)}
			</header>

			<div
				className={`
                    z-10 min-w-0
                    relative flex 
                    sm:py-4 py-2 ${displayTimes && 'md:pl-3.5'}
                `}
				key={item.id}
			>
				<div className="relative w-full leading-loose">
					<ErrorBoundary>
						{/* @ts-ignore */}
						<Component item={item} />
					</ErrorBoundary>

					<div className="mt-1 flex">
						{item.type !== 'lock' && item.fileName && (
							<div className="min-w-0 flex items-center mr-2">
								<EditorUrl
									truncateStart={true}
									path={item.path}
									fileName={item.fileName}
									lineNumber={item.lineNumber}
								/>

								{item.hostname && (
									<div className="flex items-center text-gray-500 dark:hover:text-gray-400 ml-4">
		                                <span className="w-2.5 h-2.5 mr-1 opacity-75">
		                                    <ServerIcon />
		                                </span>
										<span className="text-xs">{item.hostname}</span>
									</div>
								)}
							</div>
						)}
					</div>
				</div>

			</div>
		</div>
	);
}