import confetti from 'canvas-confetti';

export async function shootConfetti() {
	confetti({
		particleCount: 100,
		spread: 70,
		origin: { y: 0.8, x: 1 },
		angle: 125,
	});

	await sleep(200);

	confetti({
		particleCount: 100,
		spread: 70,
		origin: { y: 0.8, x: 0 },
		angle: 55,
	});
}

const sleep = (timeout: number) => {
	return new Promise((res) => setTimeout(res, timeout));
};