import { ScrollSeekPlaceholderProps } from 'react-virtuoso';
import TypeBadge from './TypeBadge';
import Tag from '@/components/ui/Tag';
import usePreferences from '@/stores/preferences';


export default function Placeholder({ height }: ScrollSeekPlaceholderProps) {
	const { displayTimes, colorblindMode } = usePreferences();
	return (
		<div className="border-gray-400 dark:border-gray-600 border-opacity-50 dark:border-opacity-50">
			<div
				style={{ transition: 'max-height 0.2s ease-out' }}
				className={`
                    log-item
                    overflow-hidden
                    relative
                    hover:bg-gray-100 dark:hover:bg-gray-700 hover:bg-opacity-25 dark:hover:bg-opacity-25
                    transition-colors duration-150 hover:duration-0
                    border-t first:border-t-0 border-gray-200 dark:border-gray-800
                `}
			>
				<div className="relative grid grid-cols-auto-1fr items-stretch">
					<nav
						className={`
                        relative h-full
                        px-2 py-4
                        ${displayTimes && 'md:bg-gray-100 md:bg-opacity-25 md:dark:bg-black md:dark:bg-opacity-25'}`}
					>
						<button
							title={displayTimes ? '--:--:--' : ''}
							className={`
                            sticky top-0
                            mt-px
                            flex items-center justify-center
                            w-4 h-4 rounded-full
                            focus:outline-none focus:ring-0
                            transform scale-50 log-item-hover:scale-100
                            transition-transform hover:duration-75 duration-150
                            border border-gray-400 text-gray-500
                        `}
						>
							<div
								className="
                                w-2 h-2
                                opacity-0 log-item-hover:opacity-100
                                transition-opacity hover:duration-75 duration-150
                            "
							>
								{/*<MinIcon />*/} -
							</div>
						</button>
					</nav>

					<div className="relative pr-3.5 min-w-0 overflow-hidden">
						<div
							style={{
								height: height,
							}}
							className={`
                                ${displayTimes && 'md:grid md:grid-cols-auto-1fr md:items-stretch'}
                                border-b last:border-b-0 border-dotted border-gray-200 dark:border-gray-800`}
						>
							<header
								className={`relative ${
									displayTimes &&
									'md:w-20 md:bg-gray-100 md:bg-opacity-25 md:dark:bg-black md:dark:bg-opacity-25 md:border-dotted md:border-gray-200 md:dark:border-gray-800 md:border-r'
								}`}
							>
								{(displayTimes || colorblindMode === 'Enabled') && (
									<div
										className={`
                                            sticky top-0 
                                            flex w-full items-center 
                                            pt-4  
                                            space-x-1 
                                            ${
											displayTimes &&
											'md:flex-col md:items-end md:py-4 md:pr-2 md:space-x-0 md:space-y-1'
										}
                                        `}
									>
										{displayTimes && (
											<Tag.Outline
												className="bg-gray-100 dark:bg-gray-900"
											>
												--:--:--
											</Tag.Outline>
										)}

										<TypeBadge
											style={{ maxWidth: '100%' }}
											itemType={'log'}
										/>

										{colorblindMode === 'Enabled' && (
											<Tag.Outline>
												gray
											</Tag.Outline>
										)}
									</div>
								)}
							</header>

							<div
								className={`
                                    z-10 
                                    relative flex 
                                    py-4 ${displayTimes && 'md:pl-3.5'}
                                `}
							>
								<div className="relative w-full leading-loose">
									<div className={`w-full markup h-full bg-gray-100 dark:bg-gray-800`}></div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
