import React from 'react';
import { TraceLogItem } from '/shared/types';
import EditorUrl from '@/components/ui/EditorUrl';
import { getFrameLabel } from '@/utils/frame';

type Props = {
    item: TraceLogItem;
};

export default React.memo(function Trace({ item }: Props) {


    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            <dl className="markup-dl">
                {item.frames.map((frame, i) => (
                    <React.Fragment key={i}>
                        <dt className={`md:text-right`}>{item.frames.length - i}</dt>
                        <dd className={`${!frame.vendor_frame && 'text-indigo-500'}`}>
                            <EditorUrl
                                path={frame.file_name}
                                fileName={frame.file_name}
                                lineNumber={frame.line_number}
                                label={getFrameLabel(frame)}
                                small={false}
                            />
                        </dd>
                    </React.Fragment>
                ))}
            </dl>
        </div>
    );
});
