import React from 'react';
import { EventLogItem } from '/shared/types';
import SymfonyData from '@/pages/main/components/SymfonyData';


export default React.memo(function Event({ item }: { item: EventLogItem }) {

    return (
        <div className={`w-full ${item.style.text ? `markup-${item.style.text}` : `markup`}`}>
            {!item.class_based_event && <code>{item.name}</code>}

            {item.class_based_event && (
                <SymfonyData id={item.id} content={item.class_based_event ? item.event : item.payload} expand={item.expand} />
            )}
        </div>
    );
});
